<template>
  <v-container fluid>
    <video ref="player" autoplay></video>
    <canvas></canvas>
    <v-btn block>Capture</v-btn>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      streamMode: true,
    };
  },
  created() {
    if (!("mediaDevices" in navigator)) {
      navigator.mediaDevices = {};
    }
    if (!("getUserMedia" in navigator.mediaDevices)) {
      navigator.mediaDevices.getUserMedia = function (constraints) {
        var getUserMedia =
          navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
        if (!getUserMedia) {
          return Promise.reject(new Error("getUserMedia is not implemented!"));
        }

        return new Promise(function (resolve, reject) {
          getUserMedia.call(navigator, constraints, resolve, reject);
        });
      };
    }

    navigator.mediaDevices
      .getUserMedia({ video: true, audio: false })
      .then((stream) => {
        this.$refs.player.srcObject = stream;
      })
      .catch(() => {
        this.streamMode = true;
      });
  },
  beforeDestroy() {
    var stream = this.$refs.player.srcObject;
    if ("stop" in stream) this.$refs.player.srcObject.stop();
    else {
      this.$refs.player.srcObject.getTracks().forEach((track) => {
        track.stop();
      });
    }
  },
};
</script>

<style scoped>
video {
  width: 100%;
}
</style>